import React from "react";
import { PageHeader } from "./PageHeader";

import "../styles/reset.css";
import { InputTabs } from "./InputTabs";
import { obsecureLastFourChar } from "../utils/obsecureLastFourChar";
import { Helmet } from "react-helmet";
interface ResetProps {
  username: string;
}

export const Reset: React.FC<ResetProps> = ({ username }) => {
  return (
    <div className="reset">
      <Helmet>
        <title>Confirm your information to regain access | HSBC</title>
      </Helmet>
      <PageHeader text="Confirm your information to regain access" />
      <div className="innerPage grid_skin">
        <div className="innerPage">
          <div className="grid_skin">
            <div className="containerStyle01">
              <div className="grid_24">
                <div className="resetDetails">
                  <div className="row">
                    <div className="row headerStyle01">
                      <h3 className="left welcome1">
                        You are logging on as:{" "}
                        <strong>{obsecureLastFourChar(username)}</strong>
                      </h3>
                      <a href="/" className="normalIcon right">
                        {" "}
                        Switch User<span className="chericon"></span>
                      </a>
                    </div>
                  </div>
                  <div className="borderbox"></div>
                  <form action="">
                    <InputTabs />
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
