import React from "react";
import { Helmet } from "react-helmet";

import "../styles/login.css";

import registerNow from "../assets/btn_register_now.jpg";
import latestScam from "../assets/20109-PWS-SAAS-login-scam-300x255.jpg";
import loginSecKey from "../assets/D650-login-seckey-300x255.jpg";
import loginCC from "../assets/D650-login-cc-300x255.jpg";
import protectingYourMoney from "../assets/protecting-your-money.jpg";
import howToStaySafeOnline from "../assets/how-to-stay-safe-online.jpg";
import appStore from "../assets/app-store.jpg";
import playStore from "../assets/google-play-logo.png";
import { PageHeader } from "./PageHeader";
import { LoginContext } from "../contexts/loginContext";

interface LoginProps {}

export const Login: React.FC<LoginProps> = () => {
  const { saveUsername } = React.useContext(LoginContext) as LoginType;
  const username = React.useRef<HTMLInputElement | null>(null);
  const [error, setError] = React.useState(false);
  return (
    <div className="login">
      <Helmet>
        <title>Log on to Online Banking: Username | HSBC</title>
      </Helmet>
      <PageHeader text="Log on to Online Banking" />
      <div className="innerPage">
        <div className="grid_skin">
          <div className="row"></div>
          {error ? (
            <div className="row">
              <div
                role="alert"
                className="alertBox"
                id="hsbcwidget_AlertBox_0"
                lang="en-GB"
                title=""
                aria-hidden="false"
              >
                <div className="alertBoxInner" role="alert">
                  <span className="hidden" aria-hidden="true">
                    Alert Box
                  </span>
                  <div data-dojo-attach-point="containerNode">
                    <br />
                    <p>Please enter your username.</p>
                  </div>
                </div>
              </div>
            </div>
          ) : null}
          <div className="row loginBoxes row_cam10_01">
            <div className="loginBox">
              <h3>Online Banking</h3>
              <form action="">
                <div className="row">
                  <label htmlFor="Username1">
                    Please enter your username eg IB1234567890 or John123
                  </label>
                </div>
                <div className="row rowwidth">
                  <div className="textInput">
                    <input type="text" id="username1" ref={username} />
                  </div>
                  <div className="button primary btnForward buttonMargin">
                    <span className="buttonInner">
                      <input
                        type="button"
                        value="Continue"
                        onClick={() => {
                          if (username.current?.value === "") {
                            setError(true);
                            return;
                          }
                          saveUsername(username.current!.value);
                        }}
                      />
                      <span className="icon"></span>
                    </span>
                  </div>
                </div>
                <div className="row">
                  <div className="checkboxContainer">
                    <div
                      className="dijit dijitReset dijitInline dijitCheckBox"
                      role="presentation"
                      lang="en-GB"
                    >
                      <input
                        name="cookieuserid"
                        type="checkbox"
                        className="dijitReset dijitCheckBoxInput"
                        id="cookieuserid"
                        value="true"
                      />
                    </div>
                    <label htmlFor="cookieuserid">Remember my username </label>
                  </div>
                </div>
                <div className="row">
                  <ul className="linkList01">
                    <li>
                      <a href="/gsa/?idv_cmd=idv.ForgotUserName&amp;FORGOT_USER=true">
                        Forgot your username?
                        <span className="chevron"></span>
                      </a>
                    </li>
                  </ul>
                </div>
              </form>
            </div>
            <div
              className="grid"
              style={{
                borderRight: "1px solid #e5e5e5",
                height: "285px",
                width: "50px",
              }}
            ></div>
            <div
              className="grid grid_9 containerStyle08 loginBox"
              style={{ marginLeft: "25px" }}
              dir="ltr"
            >
              <h3 style={{ border: "none" }}>Register for Online Banking</h3>
              <p>
                Manage your money online with our secure Online
                <br />
                Banking service.
              </p>
              <br />
              <div>
                <a href="https://www.hsbc.co.uk/register/">
                  <img
                    src={registerNow}
                    title="Register for online banking"
                    alt="Register for online banking"
                  />
                </a>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="row">
              <div
                className="row"
                style={{
                  height: "0px",
                  width: "auto",
                  borderTop: "1px solid #e5e5e5",
                }}
              ></div>
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                <div
                  className="grid grid_9 containerStyle08 loginBox"
                  style={{ marginRight: 10, paddingTop: 0 }}
                >
                  <h3 style={{ border: "none" }}>Business customers</h3>
                  <br />
                  <br />
                  <ul className="listBullets01 linkList01">
                    <li style={{ textDecoration: "underline" }}>
                      <a href="https://online-banking.business.hsbc.co.uk/portalserver/hsbc/login">
                        Log on to Business Internet Banking
                      </a>
                    </li>
                  </ul>
                  <br />
                  <div className="row" style={{ paddingBottom: 80 }}>
                    <br />
                  </div>
                  <div className="row">
                    <br />
                    <br />
                    <br />
                  </div>
                  <div style={{ display: "flex" }}>
                    <span style={{ float: "left" }}>
                      <a
                        href="https://www.hsbc.co.uk/fscs/"
                        title="FSCS Protecting your money"
                        target="_blank"
                        rel="noreferrer"
                      >
                        <img
                          alt="FSCS Protecting your money."
                          height={49}
                          src={protectingYourMoney}
                          style={{ paddingLeft: 10 }}
                          title="FSCS Protecting your money."
                          width={205}
                        />{" "}
                      </a>
                    </span>
                    <span style={{ float: "left", marginLeft: 10 }}>
                      <a
                        title="How to stay safe online. This link will open in a new browser window."
                        // onclick="window.open('https://www.hsbc.co.uk/help/security-centre/', '_blank', 'status=yes,location=no,menubar=no,resizable=yes,scrollbars=yes,toolbar=no,width=800,height=510,screenX=0,left=0,screenY=0,top=0'); return false;"
                        target="_blank"
                        href="https://www.hsbc.co.uk/help/security-centre/"
                        rel="noreferrer"
                      >
                        <img
                          src={howToStaySafeOnline}
                          title="How to stay safe online"
                          alt="How to stay safe online"
                        />
                      </a>
                    </span>
                  </div>
                  {/* END : Security and Fraud Update */}
                </div>
                <div
                  className="grid grid_1"
                  style={{ borderLeft: "1px solid #e5e5e5", height: 350 }}
                />
                <div
                  className="grid grid_9 containerStyle08 loginBox"
                  style={{ paddingTop: 0 }}
                >
                  <h3 style={{ border: "none" }}>Mobile Banking</h3>
                  <p dir="ltr" style={{ marginTop: "-20px" }}>
                    <br />
                    Manage your personal accounts easily and securely with our
                    Mobile Banking app. Set up new payments, scan cheques
                    directly into your account and place a temporary block on
                    your card. Mobile Banking your way.
                  </p>
                  <p dir="ltr" style={{ marginTop: "-10px", color: "#000000" }}>
                    <strong>Download the app</strong>
                  </p>
                  <a
                    style={{ float: "left" }}
                    href="https://itunes.apple.com/gb/app/id1220329065"
                    target="_self"
                    title="Download our app from the Apple app store"
                  >
                    <img
                      src={appStore}
                      title="Download our app from the Apple app store"
                      alt="Download our app from the Apple app store"
                    />
                  </a>
                  &nbsp;&nbsp;&nbsp;
                  <a
                    style={{ float: "left", marginLeft: 10 }}
                    href="https://play.google.com/store/apps/details?id=uk.co.hsbc.hsbcukmobilebanking"
                    target="_self"
                    title="Download our app from the Google play store"
                  >
                    <img
                      src={playStore}
                      title="Download our app from the Google play store"
                      alt="Download our app from the Google play store"
                    />
                  </a>
                  <p dir="ltr" style={{ marginTop: 70 }}>
                    <a
                      href="https://www.hsbc.co.uk/ways-to-bank/mobile/"
                      target="_self"
                      title="Find out more about Mobile Banking"
                    >
                      Find out more about Mobile Banking
                    </a>
                    &nbsp;
                  </p>
                </div>
              </div>
            </div>
            <span>
              <div dir="ltr" id="jsDOMStrMov1">
                <div className="row">
                  <div className="grid grid_8">
                    <div className="contentStrip">
                      <div>
                        <a
                          href="https://www.hsbc.co.uk/guides/security/latest-scam-warnings/"
                          title="Latest scam warnings"
                        >
                          <img
                            alt="Latest scam warnings. At HSBC we work hard to help you stay ahead of fraudsters. Keep updated on the latest types of scams"
                            style={{
                              border: 0,
                              height: "245px",
                            }}
                            src={latestScam}
                          />
                        </a>
                      </div>
                    </div>
                  </div>
                  <div className="grid grid_8">
                    <div className="contentStrip">
                      <div>
                        <a
                          href="https://www.hsbc.co.uk/help/security-centre/secure-key/"
                          title="Upgrade your Secure Key. Upgrade to our Digital Secure Key for faster and more convenient acces to your Digital Banking"
                        >
                          <img
                            alt="Upgrade your Secure Key. Upgrade to our Digital Secure Key for faster and more convenient acces to your Digital Banking. Find out more."
                            style={{
                              border: 0,
                              height: "245px",
                            }}
                            src={loginSecKey}
                          />
                        </a>
                      </div>
                    </div>
                  </div>
                  <div className="grid grid_8">
                    <div className="contentStrip">
                      <div>
                        <a
                          href="https://www.hsbc.co.uk/help/card-support/block-unblock/"
                          title="Card gone missing? Place a temporary block on your card, then unblock it if it turns up."
                        >
                          <img
                            alt="Card gone missing? Place a temporary block on your card, then unblock it if it turns up. Find out more."
                            style={{
                              border: 0,
                              height: "245px",
                            }}
                            src={loginCC}
                          />
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="grid grid_24">
                  <div className="ctaRow" style={{ fontSize: 9 }}>
                    <p style={{ paddingLeft: 35, float: "left" }}>
                      <a
                        target="_blank"
                        title="Find out more about Online Banking. This link will open in a new browser window."
                        href="https://www.hsbc.co.uk/ways-to-bank/online-banking/"
                        className="overlayLaunchLink triggerModalDetails0"
                        rel="noreferrer"
                        // onclick="window.open('https://www.hsbc.co.uk/ways-to-bank/online-banking/', '_blank', 'status=yes,location=no,menubar=no,resizable=yes,scrollbars=yes,toolbar=no,width=1000,height=545,screenX=0,left=0,screenY=0,top=0'); return false;"
                      >
                        Find out more about Online Banking
                      </a>
                    </p>
                    <p style={{ float: "left", paddingLeft: 93 }}>
                      <a
                        href="https://www.hsbc.co.uk/help/security-centre/secure-key-troubleshooting/"
                        title="Lost, damaged and stolen Secure Keys. This link will open in a new browser window."
                        target="_blank"
                        className="overlayLaunchLink triggerModalDetails0"
                        rel="noreferrer"
                        // onclick="window.open('https://www.hsbc.co.uk/help/security-centre/secure-key-troubleshooting/', '_blank', 'status=yes,location=no,menubar=no,resizable=yes,scrollbars=yes,toolbar=no,width=790,height=545,screenX=0,left=0,screenY=0,top=0'); return false;"
                      >
                        Lost, damaged and stolen Secure Keys
                      </a>
                    </p>
                    <p style={{ float: "left", paddingLeft: 105 }}>
                      <a
                        href="https://www.hsbc.co.uk/help/security-centre/what-you-can-do/"
                        title="Security downloads"
                        target="_blank"
                        className="overlayLaunchLink triggerModalDetails0"
                        rel="noreferrer"
                        // onclick="window.open(https://www.hsbc.co.uk/help/security-centre/what-you-can-do/', '_blank', 'status=yes,location=no,menubar=no,resizable=yes,scrollbars=yes,toolbar=no,width=790,height=545,screenX=0,left=0,screenY=0,top=0'); return false;"
                      >
                        Security downloads
                      </a>
                    </p>
                    &nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;
                    <p>&nbsp;</p>
                  </div>
                </div>
              </div>
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};
