import React from "react";

interface RowProps {
  title: string;
}

export const Row: React.FC<RowProps> = ({ title, children }) => {
  return (
    <div className="row1">
      <div className="question clearfix containerStyle17 jsQuestion questionGroupButtom">
        <div className="grid_skin">
          <div className="row bottomPadding0">
            <div className="containerStyle17">
              <h3>{title}</h3>
            </div>
          </div>
        </div>
        {children}
      </div>
    </div>
  );
};
